import * as React from 'react';
import { Controller, Block, RenderNodeProps } from '@ali/4ever-cangjie';
import { MobileCode } from './MobileCode';
import { BiConfig } from '../types';

export default function createRenderBlock(config: BiConfig) {
  return function renderBlock(
    props: RenderNodeProps,
    controller: Controller,
    next: () => JSX.Element,
  ) {
    const { attributes, node, isSelected } = props;
    const { type } = node as Block;
    if (Block.isBlock(node) && type === 'code') {
      return (
        <MobileCode
          controller={controller}
          attributes={attributes}
          node={node}
          locale={config.locale}
          isSelected={isSelected}
        />
      );
    }
    return next();
  };
}
